import React from "react"
import "./InformationPersonInputs_main.sass"

const FirstNameInput = (props) => {
    return (
        <div className="informationPersonInputForm7 nameInputForm7">
            <input type="text"
                   placeholder='First name'
                   name={props.name}
                   onChange={props.handleChange}
                   value={props.value}
                   onBlur={props.handleBlur}
                   className={props.errors.f_name && props.touched.f_name ? "errorsForm7" : null}
            />
            {props.errors.f_name && props.touched.f_name && (
                <div className="inputFeedbackForm7">{props.errors.f_name}</div>
            )}
        </div>
    )
};
export default FirstNameInput;